.projects-container {
    max-width: 800px;
    margin: 0 auto;
    padding: 20px;
  }
  
  .projects-list {
    list-style-type: none;
    padding: 0;
  }
  
  .publication-item {
    margin-bottom: 2rem;
    border-bottom: 1px solid #ddd;
    padding-bottom: 1rem;
  }
  
  .publication-item h3 a {
    text-decoration: none;
    color: #007acc;
  }

  .project-item {
    margin-bottom: 20px;
  }

  .project-skills {
    margin-top: 10px;
    align-items: center;
  }

  .project-skills ul {
    list-style-type: disc;
    padding-left: 20px;
  }

  .skills-list {
    display: flex;
    flex-wrap: wrap;
  }
